<template>
<div id="tagedit">
  <p v-if="tags.length===0">
    <b-alert show variant="warning">
      <strong>Please add or upload tags!</strong>
    </b-alert>
  </p>
  <div v-else>
    <table class="table">
      <thead>
      <tr>
        <th scope="col">Tag Id</th>
        <th scope="col">Description</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="tag in tags" :key="tag.url">
        <td>{{ tag.tag_id }}</td>
        <td>{{ tag.description }}</td>
        <td><button>Edit</button></td>
        <td><button>Delete</button></td>
      </tr>
      </tbody>
    </table>
  </div>
  <div>
    <form id='upload' action='/api/etag/file-upload/' method='POST' enctype='multipart/form-data'>
      <div style='display:none'>
        <input type='hidden' name='csrfmiddlewaretoken' :value='getCookie("csrftoken")'/>
      </div>
      <b-form-file v-model="file" :state="Boolean(file)" placeholder="Choose a file..."></b-form-file>
      <button id='submit'>Upload</button>
    </form>
  </div>
</div>
</template>

<script>
export default {
  name: 'EditTag',
  data () {
    return {
      tags: [],
      csrftoken: ''
    }
  },
  mounted () {
    this.getTags()
  },
  methods: {
    getTags () {
      fetch('/api/etag/tags/.json', {credentials: 'include'})
        .then(response => {
          if (!response.ok) { throw response }
          return response.json() // we only get here if there is no error
        })
        .then(json => {
          this.tags = json.results
          console.log(json)
        })
        .catch(err => {
          err.text().then(errorMessage => {
            console.log(errorMessage)
          })
        })
    },
    getCookie (cname) {
      let name = cname + '='
      let decodedCookie = decodeURIComponent(document.cookie)
      let ca = decodedCookie.split(';')
      for (let i = 0; i < ca.length; i++) {
        let c = ca[i]
        while (c.charAt(0) === ' ') {
          c = c.substring(1)
        }
        if (c.indexOf(name) === 0) {
          return c.substring(name.length, c.length)
        }
      }
      return ''
    }
  }
}
</script>



